class CarouselPanel extends React.Component {
  constructor(props) {
    super(props);
    this.state = { activeIndex: 0, isToggleOn: false, slide: false };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.onExiting = this.onExiting.bind(this);
    this.onExited = this.onExited.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(props) {
    this.setState({ isToggleOn: !this.state.isToggleOn });
  }

  onExiting() {
    this.animating = true;
  }

  onExited() {
    this.animating = false;
  }

  next() {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === items.length - 1 ? 0 : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous() {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === 0 ? items.length - 1 : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex(newIndex) {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }

  render() {
    const { activeIndex } = this.state;
    const slides = items.map((item) => {
      return (
        <CarouselItem
          onExiting={this.onExiting}
          onExited={this.onExited}
          key={item.src}
        >
          <img src={item.location} alt={item.altText} />
          <CarouselCaption captionText={item.caption} captionHeader={item.caption} />
        </CarouselItem>
      );
    });

    return (
      <Carousel
        activeIndex={activeIndex}
        next={this.next}
        previous={this.previous}
        slide={this.slide}
      >
        <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={this.goToIndex} />
        {slides}
        <CarouselControl direction="prev" directionText="Previous" onClickHandler={this.previous} />
        <CarouselControl direction="next" directionText="Next" onClickHandler={this.next} />
      </Carousel>
    );
  }

}

class Masthead extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isToggleOn: false};
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(props) {
    this.setState({ isToggleOn: !this.state.isToggleOn });
  }

  render() {
    const AiGame = ({props}) =>
    (
      <div style={{textAlign:"center"}}>
        <br/>
        <div className="caption-content">Artificial Intelligence <i className="fa fa-cog fa-spin fa-fw fa-x" style={{color:'#fff'}} onClick={this.handleClick} id="iotToolTip"/></div>
        <br/>
        <Game/>
        <br/><br/>
        <div>
          <pre style={{color: '#fff'}}>TicTacToe: Neural Network Game</pre>
        </div>
        <div className="caption-content col-sm-12">
        <Textloop springConfig={{ stiffness: 180, damping: 8 }}
          children={[
            "I'm Cloud Solution Architect 13+ Year"
          ]}
        />

        </div>
      </div>
    );

    const IoTMobility = ({props}) =>
    (
      <Col xs="12" sm="6" lg="6" style={{textAlign:"center"}}>
        <br/>
        <div className="caption-content">IoT Barcode Mobility <i className="fa fa-cog fa-spin fa-fw fa-x" style={{color:'#fff'}} onClick={this.handleClick} id="iotToolTip"/>
          <Tooltip placement="auto" isOpen={this.state.tooltipOpen} target="iotToolTip" toggle={this.toggle}/>
        </div>
        <Snack/>
        <a href="https://itunes.apple.com/app/apple-store/id982107779?ct=www&amp;mt=8" 
          target="_blank">
          <img src={AppleLogo} alt="Apple Play Store" height="40" width="136"/>
        </a>&nbsp;
        <a href="https://play.google.com/store/apps/details?id=host.exp.exponent&amp;referrer=www" 
              target="_blank">
          <img src={GoogleLogo} alt="Google Play Store" height="40" width="136"/>
        </a>
        <br/><br/>
        <div className="caption-content ">
        <Textloop 
          children={[
            "I'm Cloud Solution Architect 13+ Year",
            "Force.com Sales, Service & Community",
            "Apex, Sql, Nodejs, Kotin, Java, ESB",
            "Polyglot API Integration & IC Cloud",
            "SSO, OAuth, OpenID, 2Way SSL, SAML",
            "Automate Process, Offline Data Sync",
            "Data Migration, Outbound Messaging",
            "Visit Again for Updates !M SHANDILYA!"
          ]}
        /> 

        </div>
      </Col>
    );

    let headerPanel = null; let colFix = null; 
    if (!this.state.isToggleOn) headerPanel = <IoTMobility/>
    else {
      colFix = <Col lg="1" />
      headerPanel = <AiGame/>
    }
    return (
      <Jumbotron>
        <div className="container-fluid text-center">
            
            <Row className="row vertical-align">
                <Col xs="12" sm="6" lg="6">
                  <br/>
                  <CarouselPanel/>
                </Col>
                {colFix} 
                {headerPanel}
            </Row>
        </div>
      </Jumbotron>
    );
  }
}

export default Masthead;



