//import theme from '../themes/theme';

const gameTheme = {
  positions: {
    x: {
      lineColor: '#009ddc',
      lineWidth: 4,
      percentageByFrame: 15,
      delayAfterEachLine: 10
    },
    o: {
      lineColor: '#009ddc',
      lineWidth: 4,
      percentageByFrame: 5
    }
  },
  boardLines: {
    lineColor: '#009ddc',
    lineWidth: 2,
    percentageByFrame: 10,
    delayAfterEachLine: 10
  },
  winLine: {
    lineColor: '#fff',
    lineWidth: 8,
    percentageByFrame: 10,
    delayAfterEachLine: 10
  }
};

export default gameTheme;



