import React, { Component } from 'react';
//import './App.css'; ecf0f1 34495e
import { SnackSession } from 'snack-sdk';
import QRCode from 'qrcode.react';

const INITIAL_CODE = `
import React, { Component } from 'react';
import { Text, View, StyleSheet } from 'react-native';
import { Constants } from 'expo';
import { Card } from 'react-native-elements';
export default class App extends Component {
  render() {
    return (
      <View style={styles.container}>
        <Text style={styles.paragraph}>
          This is connected device demo by SYSFORGE - https://sysforge.github.io/
        </Text>
        <Card title="Madhav Shandilya's">
          <Text style={styles.paragraph}>
            Cloud Solution Architect since 13+ Years... With objective
            to integrate force.com with open source enterprise friendly library 
            & demonstrates the unique value business proposition for Customer Engagement.
          </Text>
        </Card>
      </View>
    );
  }
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    paddingTop: Constants.statusBarHeight,
    backgroundColor: '#03354C',
  },
  paragraph: {
    margin: 24,
    fontSize: 18,
    fontWeight: 'bold',
    textAlign: 'center',
    color: '#61dafb',
  },
});
`;

class App extends Component {
  constructor(props) {
    super(props);

    const code = INITIAL_CODE;
    this._snack = new SnackSession({
      code,
      // sessionId is optional, will be assigned a random value if not specified
      sessionId: Math.random()
        .toString(36)
        .substr(2, 8),
      sdkVersion: '22.0.0',
    });

    this._logSubscription = this._snack.addLogListener(this._onLog);
    this._errorSubscription = this._snack.addErrorListener(this._onError);
    this._presenceSubscription = this._snack.addPresenceListener(this._onPresence);

    this.state = {
      url: '',
      code,
      log: null,
      error: null,
      presence: null,
      saveUrl: null,
    };
  }

  componentWillMount() {
    this._startSnack();
  }

  async _startSnack() {
    await this._snack.startAsync();
    let url = await this._snack.getUrlAsync();
    this.setState({
      url,
    });
  }

  _onChangeCode = async event => {
    const code = event.target.value;
    this.setState({
      code,
    });
    await this._snack.sendCodeAsync(code);
  };

  _onLog = log => {
    this.setState({
      log: JSON.stringify(log, null, 2),
    });
  };

  _onError = error => {
    this.setState({
      error: JSON.stringify(error, null, 2),
    });
  };

  _onPresence = presence => {
    this.setState({
      presence: JSON.stringify(presence, null, 2),
    });
  };

  _removeListeners = () => {
    this._logSubscription.remove();
    this._errorSubscription.remove();
    this._presenceSubscription.remove();
  };

  _save = async () => {
    let saveResult = await this._snack.saveAsync();
    this.setState({
      saveUrl: saveResult.url,
    });
  };

  render() {
    let deviceLog = null;
    //const isConnected = this.state.presence == ``;
    if (this.state.presence == null) deviceLog = <DeviceConnector msg="sysforge: scan from expo mobile"/>;
    else deviceLog = <DeviceConnector msg={this.state.presence}/>;
    return (
      <div>
        <div style={{ padding: 20 }}>
          <QRCode value={this.state.url} />
        </div>
        {deviceLog}
        {this.state.saveUrl && (
          <div style={{ padding: 20 }}>
            <QRCode value={this.state.saveUrl} />
          </div>
        )}
      </div>
    );
  }
}

const DeviceConnector = ({msg}) =>
(   <div>
      <pre style={{color: '#fff'}}>{msg}</pre>
    </div>
); 

export default App;


