import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { newGame, selectPosition } from '../../state/actions';
//import NewgameBtn from './newgame';
import Canvas from './_canvas';
import AiIcon from 'react-icons/lib/fa/user-secret';
import UserIcon from 'react-icons/lib/fa/user';
//import { FormattedMessage } from 'react-intl';
import FaTerminalLogo from 'react-icons/lib/fa/terminal';
import styled from 'styled-components';
//import { rhythm, scale, options } from "../utils/typography"

const Section = styled.section`
  display: flex;
  flex-direction: column;
  margin: auto;

  width: 100%;
  max-width: 20rem;

  text-align: center;
`;

const Score = styled.div`
  padding-bottom: 1rem;

  svg {
    font-size: ${({ theme }) => 5};
  }
  
  span, svg {
    vertical-align: middle;
    display: inline-block;
  }

  .points {
    font-size: ${({ theme }) => 7};
    font-weight: bold;    
    padding: 0 ${({ theme }) => 9};
    top: 0.1rem;
    position: relative;
  }
`;

const getWinLostMsg = (game, forAi) => {
  if (!game.ended) {
    return null;
  }

  const id = game.winners && game.isAiTurn !== forAi
    ? 'won'
    : 'lost';

  return id;//(<FormattedMessage id={id} />);
};

class Game extends React.PureComponent {

  render() {
    const { game, selectPosition } = this.props;

    return (
      <Section>
        <Score>
          {getWinLostMsg(game, false)}
          <UserIcon />
          <span className="points">{game.score.human}</span>
          &nbsp;<span>:</span>&nbsp;
          <span className="points">{game.score.ai}</span>
          <AiIcon />
          {game.isAiTurn && !game.ended ? <FaTerminalLogo color='#009ddc'/>  : null}
          {getWinLostMsg(game, true)}
        </Score>
        <Canvas
          game={game}
          selectPosition={selectPosition}
        />
      </Section>
    );
  }
};

Game.propTypes = {
  game: PropTypes.object.isRequired,
  selectPosition: PropTypes.func.isRequired,
  newGame: PropTypes.func.isRequired
};

const mapStateToProps = state => {
  return {
    game: state.game
  };
};

const mapDispatchToProps = dispatch => {
  return {
    newGame: type => {
      dispatch(newGame(type));
    },
    selectPosition: index => {
      dispatch(selectPosition(index));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Game);



