import React from 'react';
import Link from 'gatsby-link';
import {
  Collapse,
  Nav,
  Navbar,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  NavLink,
  Badge
} from 'reactstrap';

import FaTerminalLogo from 'react-icons/lib/fa/terminal';
import FaCloudLogo from 'react-icons/lib/fa/cloud';
import FaGithubLogo from 'react-icons/lib/fa/github';


const navItems = [
  { href: '/sysforge', label: 'sysforge' },
  { href: '/blog', label: 'Blog' },
  { href: '/mobility', label: 'Mobility' },
  { href: '/contact', label: 'Contact' },
];

class Header extends React.Component {

  constructor(props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
    };
    this.handleClick = this.handleClick.bind(this);
  }

  toggleNavbar() {
    this.setState({
      isToggleOn: false,
      collapsed: !this.state.collapsed
    });
  }

  handleClick(props) {
    this.setState(prevState => ({
      isToggleOn: !prevState.isToggleOn,
      mobilePanel: true
    }));
    setTimeout(function() { 
      this.setState(prevState => ({
      isToggleOn: false
      }));
    }.bind(this), 2500);
  }

  sidebarToggle(e) {
    e.preventDefault();
    document.body.classList.toggle('sidebar-hidden');
  }

  sidebarMinimize(e) {
    e.preventDefault();
    document.body.classList.toggle('sidebar-minimized');
  }

  mobileSidebarToggle(e) {
    console.log('mobile naav called..');
    e.preventDefault();
    document.body.classList.toggle('sidebar-mobile-show');
  }

  asideToggle(e) {
    e.preventDefault();
    document.body.classList.toggle('aside-menu-hidden');
  }

  render() {
    return (
      <header className="app-header navbar ">
        <NavbarToggler onClick={this.sidebarToggle}>
            <NavLink href="https://github.com/sysforge/sysforge.github.io" style={{color: '#fff', justifyContent: 'center'}}>
              <FaGithubLogo color='#fff' size='28'/>&nbsp;'M SHANDILYA
            </NavLink>
        </NavbarToggler>
        <Nav className="d-md-down-none" navbar>
          {
            navItems.map(item =>
              <NavItem className="px-3" key={item.label}>
                <NavLink href={item.href}>{item.label}</NavLink>
              </NavItem>
            )
          }
        </Nav>
        <NavbarToggler onClick={this.mobileSidebarToggle}>
          <span className="navbar-toggler-icon"></span>
        </NavbarToggler>
      </header>
    );
  }
}

export const Childhead = ({props, currentpage}) =>
(
    <header className="masthead">
      <div className="container">
        <div className="intro-text">
          <div className="intro-lead-in">Welcome To Our Studio!</div>
          <div className="intro-heading text-uppercase">It's Nice To Meet You</div>
          <a className="btn btn-primary btn-xl text-uppercase js-scroll-trigger" href="#services">Tell Me More</a>
        </div>
      </div>
    </header>
);  

export default Header;



