import React from 'react';
import Link from 'gatsby-link';
import HeartLogo from 'react-icons/lib/fa/heart';
import Beerlogo from 'react-icons/lib/fa/beer';
import FaFacebook from 'react-icons/lib/fa/facebook';
import FaTwitter from 'react-icons/lib/fa/twitter-square';
import FaLinkedin from 'react-icons/lib/fa/linkedin-square';
import FaYoutube from 'react-icons/lib/fa/youtube-square';
import FeedbackLogo from '../../images/opinionlab-orange.gif';
import FaGithub from "react-icons/lib/fa/github-alt";//pied-piper-alt
import moment from 'moment';
import VoiceIO from '../Audio/Audio';

import {
  Row,
  Col
} from 'reactstrap';

class Footer extends React.Component {
  render() {
  	const updated = moment().format('MMM Do YYYY, h:mm a');
    let voicePanel = null;
    //if (isChrome) { not rendered on android
      //const VoiceIO = require('../Audio/Audio');
      voicePanel = <VoiceIO/>
    //}
    return (
      <footer className="app-footer">
        <div className="container">
          <Row>
            <Col xs="12" sm="12" lg="12">
              <div className="footer-copyright">
                Built with <HeartLogo color='#FF0000' size='16'/> by <u>Madhav Shandilya</u> &nbsp;
                <Link  to="/contact">Various trademarks held by their respective owners. </Link>
              </div>
            </Col>
          </Row>
          <br/>
          <Row>
            <Col xs="12" sm="6" lg="6">
            <div className="footer-copyright">
                    <img src={FeedbackLogo} alt="Feedback"/>
                    &nbsp; V..@NXG rendered on {updated} &nbsp;

              </div>
              </Col>
              <Col xs="12" sm="6" lg="6">
              <ul className="social-icons unstyled">
                    <li>
                      <a href="https://www.linkedin.com/in/expressinc/" target="_blank"><FaLinkedin color='#fff' size='28'/></a>
                    </li>
                    <li>
                      <a href="https://www.facebook.com/madhav.shandilya.7" target="_blank"><FaFacebook color='#fff' size='28'/></a>
                    </li>
                    <li>
                      <a href="https://www.youtube.com/channel/UCtSNWJDGqQwXUqmx9IaHEfQ?disable_polymer=true" target="_blank"><FaYoutube color='#fff' size='28'/></a>
                    </li>
                    <li>
                      <a href="https://github.com/sysforge/" target="_blank">&nbsp;&nbsp;<FaGithub color='#fff' size='28'/></a>
                    </li>&nbsp;
                    <li>
                      {voicePanel}
                    </li>
                  </ul>
            </Col>
          </Row>
        </div>
      </footer>
    )
  }
}

export default Footer;



