import React from 'react';
import Helmet from 'react-helmet';
//import RBootstrap from '../components/temp/react-bootstrap';

//import Footer from '../components/footer';
import store from '../state/store';

import Header from '../components/Header/Header';
import HeaderMaster from '../components/Header/Masthead';
import HeaderChild from '../components/Header/Childhead';
import Sidebar from '../components/Sidebar/Sidebar';
import Aside from '../components/Aside/Aside';
import Footer from '../components/Footer/Footer';
import { Container } from 'reactstrap';

//import styled from 'styled-components'; `backtick`
import { Provider } from 'react-redux';
import '../scss/styles.scss';

// Import Simple Line Icons Set
//import 'simple-line-icons/css/simple-line-icons.css';
// Temp fix for reactstrap
//import '../scss/coreui/_dropdown-menu-right.scss'


//Raven.config('https://7cb60fa2cbca11e7a39b4201c0a8d046@sentry.io/98830',
    //{ whitelistUrls: [ /https?:sysforge?github\\.io/ ] }
  //).install();
export const sentryScript = `Raven.config('https://cca7b0035fbe444fb55185b34f8104f8@sentry.io/170238').install()`;
//<script src="https://cdn.ravenjs.com/3.20.1/raven.min.js" />
//<script dangerouslySetInnerHTML={{__html: sentryScript}} />
class TemplateWrapper extends React.Component {
  componentDidCatch(error, errorInfo) {
        this.setState({ error });
        //Raven.captureException(error, { extra: errorInfo });
    }
  render() {
    let background = null;
    const isHome = this.props.location.pathname == `/`;
    if (isHome) background = <HeaderMaster/>;
    else background = <HeaderChild currentpage={this.props.location.pathname}/>;

    return (
      <div className="app">
        <Helmet
        title="'M Shandilya: Cloud Solution Architect's, Portfolio!"
        meta={[
          { name: 'description', content: 'M Shandilya Portfolio' },
          { name: 'keywords', content: 'Madhav Shandilya, Github, Sysforge' },
          { name: 'viewport', content: 'width=device-width, initial-scale=1.0' },
        ]}/>
        <Header/>

        <div className="app-body">

          <Sidebar/>
          <main className="main">
            <Container fluid>

              {background}
              {this.props.children()}
            </Container>
          </main>
          <Aside/>
        </div>

        <Footer/>
      </div>
    )
  }
}

export const Childhead = ({props, currentpage}) =>
(
    <header className="masthead">
      <div className="container">
        <div className="intro-text">
          <div className="intro-lead-in">Welcome To Our Studio!</div>
          <div className="intro-heading text-uppercase">It's Nice To Meet You</div>
          <a className="btn btn-primary btn-xl text-uppercase js-scroll-trigger" href="#services">Tell Me More</a>
        </div>
      </div>
    </header>
);

const SysforgeLayout = (props, context, c) => {
  return (
    <Provider store={store}>
      <TemplateWrapper {...props} />
    </Provider>
  );
};

export default SysforgeLayout;



