import React from 'react';
//import Img from 'gatsby-image';
import Link from 'gatsby-link';
import YouTube from 'react-youtube';

//import HeroLogo from '../images/cross-stitch-site-name.png';
//L24: <h1><img src={HeroLogo} alt="Sysforge Lab" title="Sysforge Lab" /></h1>
//L29: <YouTube videoId='RtEwlMiJoug'/>
import EinsteinLogo from '../../images/einstein-icon.png';
import PropTypes from "prop-types";

import {
  Breadcrumb, 
  BreadcrumbItem,
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
  Col,
  Jumbotron,
  Row
} from 'reactstrap';

export const Childhead = ({props, currentpage}) =>
(
    <header className="sb-page-header">
        <div className="container">
            <ul className="breadcrumb">
                <li><Link to="/">Home</Link></li>                
                <li className="current-breadcrumb">{currentpage}</li>
            </ul>
            <div className="row vertical-align">
                <div className="title-bar-header vertical-align col-sm-9">
                    <h1>{currentpage}</h1>
                </div>
            <Game/>
            </div>
            
        </div>
    </header>
);


class ChildheadFn extends React.Component {//<Game/> L65
    constructor(props) {
        super(props);
    }
    render() {
        let childHeaderMsg = null;
        if (this.props.currentpage == '/blog' || this.props.currentpage == '/blog/') childHeaderMsg = 'Blog: Cloud Architecture'
        else if (this.props.currentpage == '/mobility' || this.props.currentpage == '/mobility/') childHeaderMsg = 'Design: Connected Apps'
        else if (this.props.currentpage == '/contact' || this.props.currentpage == '/contact/') childHeaderMsg = "Feedback: Geocode Map"
        return (
            <header className="sb-page-header">
                <div className="container-fluid">
                    
                    <div className="row vertical-align">
                        <div className="title-bar-header vertical-align col-sm-12">
                            <h1 style={{color: '#048ec6'}}>{childHeaderMsg}</h1>
                        </div>
                    </div>
                </div>
            </header>
        )
    }
}


export default ChildheadFn;



