import React from 'react';
// Import the Artyom library
import Artyom from '../../library/artyom.js';

// Import the previously created class to handle the commands from another file
import VoiceManager from './_microphone';

import FaMicrophone from  "react-icons/lib/fa/microphone";
import FaClose from  "react-icons/lib/fa/close";
import Link from 'gatsby-link';
//import GoogleMaps from '../components/maps'
//import Modal from "react-modal"
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { navigateTo } from "gatsby-link";
// ArtyomCommands.js
// Create a "globally" accesible instance of Artyom
const Jarvis = new Artyom();


//export default class AudioManager {
class AudioManager extends React.Component {
  constructor (props, context){
    super(props, context);

        // Add `this` context to the handler functions
    this.startAssistant = this.startAssistant.bind(this);
    this.stopAssistant = this.stopAssistant.bind(this);
    this.speakText = this.speakText.bind(this);
    this.handleTextareaChange = this.handleTextareaChange.bind(this);

    // Prepare simple state
    this.state = {
      artyomActive: false,
      textareaValue: "",
      artyomIsReading: false
    };

    // Load some commands to Artyom using the commands manager
    let CommandsManager = new VoiceManager(Jarvis);
    CommandsManager.loadCommands();
  }

  startAssistant() {
    let _this = this;

    console.log("AudioManager Started...");

    Jarvis.initialize({
      lang: "en-GB",
      debug: false,
      continuous: true,
      soundex: true,
      listen: true
    }).then(() => {
      // Display loaded commands in the console
      //console.log(Jarvis.getAvailableCommands());
      Jarvis.say("ask madhav shandilya");

      _this.setState({
        artyomActive: true
      });
      }).catch((err) => {
        console.error("Oopsy daisy, this shouldn't happen !", err);
    });
  }

  stopAssistant() {
    let _this = this;

    Jarvis.fatality().then(() => {
      console.log("AudioManager Stopped...");

      _this.setState({
        artyomActive: false
      });

    }).catch((err) => {
      console.error("Oopsy daisy, this shouldn't happen neither!", err);

      _this.setState({
        artyomActive: false
      });
    });
  }

  speakText() {
    let _this = this;

    _this.setState({
      artyomIsReading: true
    });

    // Speak text with Artyom
    Jarvis.say( _this.state.textareaValue, {
      onEnd() {
        _this.setState({
        artyomIsReading: false
      });
    }
    });
  }

  handleTextareaChange(event) {
    this.setState({
      textareaValue: event.target.value
    });
  }

  render(){
    let voiceModal = null;
    if (this.state.artyomActive) voiceModal = <voice/>
    return (
    <div>
      <FaMicrophone color='#fff' size='28' onClick={this.startAssistant} disabled={this.state.artyomActive}/>
      <Modal isOpen={this.state.artyomActive}
      style={{
            position: `relative`,
            border: `none`,
            background: `#03354C`,
            overflow: `auto`,
            WebkitOverflowScrolling: `touch`,
            zIndex: '9998',
            backgroundColor: `rgba(0, 0, 0, 0.75)`
        }}>
        <ModalHeader><div style={{color:'#000', justifyContent: 'center'}}>AUDIO PROFILE</div></ModalHeader>
        <ModalBody >
          <div style={{color:'#000', justifyContent: 'center'}}>Call from Chrome and Ask</div>
          <div style={{color:'#000', justifyContent: 'center'}}><u>shandilya</u>, <u>bye</u>, <u>see you later</u></div>
        </ModalBody>
        <ModalFooter>
          <FaClose color='#000' size='64' onClick={this.stopAssistant}/>
        </ModalFooter>
      </Modal>
    </div>
    );
  }
}
//#61dafb
const voice = () =>
(
<div id="myModal" className="modal fade" role="dialog">
  <div className="modal-dialog">

    <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal">&times;</button>
        <h4 className="modal-title">Modal Header</h4>
      </div>
      <div className="modal-body">
        <p>Some text in the modal.</p>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
      </div>
    </div>

  </div>
</div>
);

export default AudioManager;



